
import DownloadTable from '~/components/DownloadTable'

export default {
  components: {
    DownloadTable
  },
  props: {
    year: {
      type: Number,
      required: true
    },
    // see assets/stats for example
    definition: {
      type: Object,
      required: true
    }
  },
  data () {
    return {}
  },
  computed: {
    jsonContent () {
      return (
        'data:text/plain;charset=utf-8,' +
        encodeURIComponent(JSON.stringify(this.definition, null, 2))
      )
    }
  }
}
